import React from 'react';
import NewsCard from '../NewsCard/NewsCard';
import { Grid, Grow, Typography } from '@material-ui/core';
import useStyles from './styles.js';

const infoCards = [
    { color: '#0d47a1', title: 'Latest News', text: 'Give / Show me the latest news' },
    { color: '#0d47a1', title: 'News by Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
    { color: '#0d47a1', title: 'News by Terms / Names', info: 'Bitcoin, PlayStation 5, Smartphones, Donald Trump...', text: 'What\'s up with PlayStation 5' },
    { color: '#0d47a1', title: 'News by Sources', info: 'CNN, Wired, BBC News, Time, IGN, Buzzfeed, ABC News...', text: 'Give me the news from CNN' },
  ];

const NewsCards = ({ articles, activeArticle }) => {
    const classes = useStyles();

    if (!articles.length){
        return (
            <Grow in>
                <Grid className={classes.container} container alignItems="stretch" spacing={3}>
                {infoCards.map((infoCard) => (
                    <Grid item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}>
                        <div className={classes.card} style={{ backgroundColor: infoCard.color }}>
                            <Typography variant="h4" style={{color: '#e9d8a6'}} component="h4">{infoCard.title}</Typography>
                            {infoCard.info ? <Typography variant="h7" component="h7"><strong>{infoCard.title.split(' ')[2]}</strong>: <br />{infoCard.info}</Typography> : null}
                            <Typography style={{color: '#ca6702'}} variant="h6" component="h6"> <b> Try saying: </b> <br /> <i>{infoCard.text}</i></Typography>
                        </div>
                    </Grid>
                ))}
                </Grid>
            </Grow>
        );
    }

    return (
        <Grow in>
            <Grid className={classes.container} container alignItems="stretch" spacing={3}>
            {
                articles.map((article, i) => (
                    <Grid item xs={12} sm={6} md={4} lg={3} style={{display: 'flex'}}>
                        <NewsCard activeArticle={activeArticle} article={article} i={i} />
                    </Grid>
                ))
            } 
            </Grid>
            
        </Grow>
    );
};

export default NewsCards;