import React, { useState, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import wordsToNumbers from 'words-to-numbers';

import alanBtn from '@alan-ai/alan-sdk-web';
import NewsCards from './components/NewsCards/NewsCards';
import useStyles from './styles.js';
import image from './images/AI-image-unsplash.jpg';
import './index.css';

const alanKey = 'bfbec2f218cfa0c6fed065826b45c22a2e956eca572e1d8b807a3e2338fdd0dc/stage';

const App = () => {
  const [newsArticles, setNewsArticle] = useState([]);
  const [activeArticle, setActiveArticle] = useState(-1);
  const [isOpen, setIsOpen] = useState(false);

  const classes = useStyles();
  
  useEffect(() => {
    alanBtn({
      key: alanKey,
      onCommand: ({ command, articles, number }) => {
        if(command === 'newHeadlines'){
          setNewsArticle(articles);
          setActiveArticle(-1);        
        } else if (command === 'instructions') {
          setIsOpen(true);
        }
        else if (command === 'highlight') {
          setActiveArticle((prevActiveArticle) => prevActiveArticle + 1);
        } 
        else if (command === 'open') {
          const parsedNumber = number.length > 2 ? wordsToNumbers((number), { fuzzy: true }) : number;
          const article = articles[parsedNumber - 1];

          if (parsedNumber > articles.length) {
            alanBtn().playText('Please try that again...');
          } else if (article) {
            window.open(article.url, '_blank');
            alanBtn().playText('Opening...');
          }
           else {
            alanBtn().playText('Please try that again...');
          }
      }
    },
  });
}, [] );

  return (
    <div>
      <div id="introText"  >
        <Typography style={{fontFamily:"'Electrolize', sans-serif",
        fontFamily:"'Gluten', cursive" }} variant="h1" component="h1"> Va Zu Dev </Typography>
        <Typography variant="h4" component="h4"> AI-Based News Assistant </Typography>
      </div>
      <div className={classes.logoContainer}>
        {newsArticles.length ? (
          <div className={classes.infoContainer}>
            <div className={classes.card}><Typography variant="h5" component="h3">Try saying: <br /><br />Open article [4]</Typography></div>
            <div className={classes.card}><Typography variant="h5" component="h3">Try saying: <br /><br />Go back Home</Typography></div>
          </div>
        ) : null}
        <img src={image} className={classes.alanLogo} alt="logo" />
      </div>
      <NewsCards articles = {newsArticles} activeArticle={activeArticle} />
    </div>

  );
}

export default App;